import { isKeyValid } from '../../support/util';

/**
 *  computed 
 * defComputed((newState, oldState)=>{
 *   // newState oldStatecuObState
 * })
 * @param {{[key:string]:any}} state 
 * @param {string[]} depKeys 
 */
export default function (state, depKeys) {
  return new Proxy(state, {
    get: function (target, key) {
      /**
       * isKeyValidstatecomputed valuekey
       *   ctx.computed('count', n => {
       *     return n * 2;//  return n.count * 2
       *    })
       *   //  n.count * 2,  n * 2 key
       *   // valueOf, toString, Symbol(...)
       */
      if (isKeyValid(target, key) && !depKeys.includes(key)) depKeys.push(key);

      return target[key];
    },
    // set: function (target, key) {
    set: function () {
      // do nothingcomputedstate
      return true;
    }
  });
}
